export const cambiaTipo = (tipo) => {
    return {
        type: 'CAMBIA_TIPO',
        payload: tipo
    };
}

export const cambiaRange = (range) => {
    return {
        type: 'CAMBIA_RANGE',
        payload: range
    };
}


export const cambiaPaesi = (paesi) => {
    return {
        type: 'CAMBIA_PAESI',
        payload: paesi
    };
}
