import moment from "moment";
import React from "react";
import { Row, Col } from "antd";
import { useSelector } from 'react-redux';
import 'antd/dist/antd.css';

import Grafico from "./Grafico";
import Opzioni from "./Opzioni";
import { dati } from '../assets/dati';
import PAESI from "../constants/paesi";
import { COMMON_PROPS, VERSIONE, DATA_AGGIONAMENTO } from "../constants/conf";

const giorni = (range) => {
    let etichette = [];
    let giornoInizio = moment().subtract(-range[0], 'days');

    let dataAggiornamento = moment(DATA_AGGIONAMENTO + " 23:59:ss", "DD/MM/YYYY HH:mm:ss");
    let giornoFinale = dataAggiornamento.subtract(-range[1], 'days');

    while (giornoInizio <= giornoFinale) {
        etichette.push(giornoInizio.format("DD/MM/YYYY"));
        giornoInizio.add(1, 'days');
    }

    return etichette;
}


const recuperaDatasetPaese = (paese, tipo, gg) => {

    let datiPaesePerTipo = gg.map((giorno) => {
        let dato = dati.find((elem) => elem.DateRep === giorno && elem.CountryExp === paese);
        if (dato == null)
            return 0;
        return dato[tipo];

    });


    let ilPaese = PAESI.find(p => p.codice === paese);

    let x = {
        label: ilPaese.descrizione,
        data: datiPaesePerTipo,
        borderColor: ilPaese.borderColor,
        backgroundColor: ilPaese.backgroundColor

    };

    return (
        Object.assign(x, COMMON_PROPS)
    )
}

////////////////////////////////////////////////////////////////
// RICALCOLO
////////////////////////////////////////////////////////////////


const ricalcolaGrafico = (tipo, range, paesi) => {

    let gg = giorni(range);
    let newDataSets = paesi.map(PAESE => recuperaDatasetPaese(PAESE, tipo, gg));

    return ({
        labels: gg,
        datasets: newDataSets
    });

}





/////////////////////////////////////////////////////////////////////
// MAIN
/////////////////////////////////////////////////////////////////////

const Main = (props) => {

    const tipo = useSelector(state => state.tipo);
    const range = useSelector(state => state.range);
    const paesi = useSelector(state => state.paesi);

    return (

        <div>

            <Row>
                <Col span={24}>
                    <h1 className="titolo">Situazione contagi / decessi per Coronavirus al {DATA_AGGIONAMENTO}</h1>
                </Col>
            </Row>

            <Row>
                <Col span={24}>
                    <Opzioni></Opzioni>

                </Col>
            </Row>
            <Row>
                <Col span={24}>
                    <div className="grafico">
                        <Grafico data={ricalcolaGrafico(tipo, range, paesi)}></Grafico>
                    </div>
                </Col>
            </Row>
            <Row>
                <Col>
                    <p className="alert">Attenzione! L'applicazione  ancora in  fase di sviluppo per cui sono possibili errori e malfunzionamenti</p>
                </Col>
            </Row>
            <Row>
                <Col>
                    <div className="msg">
                        (*) I dati della Spagna sono comunicati con un ritardo di 24 ore
                        </div>
                </Col>
            </Row>
            <Row>
                <Col>
                    I dati sono stati resi disponibili da:
                    </Col>
            </Row>
            <Row>
                <Col>
                    <a href="https://www.ecdc.europa.eu/en/publications-data/download-todays-data-geographic-distribution-covid-19-cases-worldwide"
                    >European Centre for Disease Prevention and Control</a>
                </Col>
            </Row>
            <Row>
                <Col>
                    Chart CV - Versione {VERSIONE}
                        </Col>
            </Row >
            <Row>
                <Col>
                    <a href="mailto:franco.vuoto@gmail.com">Franco Vuoto</a> - Bologna, 14/03/2020
                        </Col>
            </Row >

        </div >
    );
}

export default Main;

