import { Col, Radio, Row, Slider, Switch } from 'antd';
import 'antd/dist/antd.css';
import React from "react";
import { DATA_AGGIONAMENTO } from "../constants/conf";
import PAESI from "../constants/paesi";

import moment from "moment";

import { useSelector, useDispatch } from 'react-redux';
import { cambiaTipo, cambiaRange, cambiaPaesi } from "../actions";



const Opzioni = (props) => {

    const tipo = useSelector(state => state.tipo);
    const range = useSelector(state => state.range);
    const paesi = useSelector(state => state.paesi);

    const INIT_SLIDER = moment(DATA_AGGIONAMENTO, "DD/MM/YYYY").diff(moment("01/01/2020", "DD/MM/YYYY"), 'days');

    const dispatch = useDispatch();


    ////////////////////////////////////////////////////////////////
    //// RENDER
    ////////////////////////////////////////////////////////////////

    return (
        <div>
            <Row>
                <Col span={24}>
                    <fieldset className={"opzfieldset"}>
                        <legend className={"opzlegend"}> Opzioni </legend>


                        <div className="switchPaesi">
                            {PAESI.map(paese => renderCheck(paese.codice, paese.descrizione, paesi, dispatch))}
                        </div>

                        <div className="tipoStats">

                            <Radio.Group name="tipo" onChange={(event) => dispatch(cambiaTipo(event.target.value))}
                                value={tipo}
                                buttonStyle="solid"
                            >
                                <Radio.Button style={{ "margin": "2px" }} value={"SumDeaths"}  >Totale decessi</Radio.Button>
                                <Radio.Button style={{ "margin": "2px" }} value={"SumCases"}>Totale contagiati</Radio.Button>
                                <Radio.Button style={{ "margin": "2px" }} value={"NewDeaths"}>Nuovi decessi</Radio.Button>
                                <Radio.Button style={{ "margin": "2px" }} value={"NewConfCases"}>Nuovi contagiati</Radio.Button>
                            </Radio.Group>
                        </div>

                        <div className="slider">
                            Periodo di rilevazione:
                                <Slider min={-INIT_SLIDER} max={0} step={1} range value={range}

                                tipFormatter={formatTootip}
                                onChange={(event) => dispatch(cambiaRange(event))} />
                        </div>
                    </fieldset>
                </Col>
            </Row>

        </div>

    );
}


////////////////////////////////////////////////////////////////
//// RENDERING PARZIALI
////////////////////////////////////////////////////////////////

/***************************************************
 * rendering degli switch degli stati
 ***************************************************/
const renderCheck = (codice, descrizione, paesi, dispatch) => {

    return (<Switch checkedChildren={descrizione} key={codice}
        unCheckedChildren={descrizione}
        onChange={handleCheck(codice, dispatch, paesi)} checked={paesi.includes(codice)}
    />)
};


/***************************************************
 * controllo dello switch degli stati
 ***************************************************/

const handleCheck = (codice, dispatch, paesi) => (checked, event) => {
    let newpaesi = null;
    if (checked) {
        newpaesi = [...paesi, codice]
    } else {
        newpaesi = paesi.filter(p => p !== codice);
    }
    dispatch(cambiaPaesi(newpaesi))
};
























/***************************************************
 * etichetta dei tooltip dello slider
 ***************************************************/

const formatTootip = (valore) => {

    let numGiorni = -parseInt(valore);

    if (numGiorni === 0) return DATA_AGGIONAMENTO;
    if (numGiorni === 1) return "1 giorno prima";
    return numGiorni + " giorni prima"

}

export default Opzioni;

